const rootReducers = (state = initState , action) => {
    
    if(action.type === "DELETE_POST"){
        let newPost = state.posts.filter(post => {
            return post.id !== action.id
        });

        return {
            ...state,
            posts:newPost
        }
    }
    
    return state
    
}

export default rootReducers;